.albumPhoto {
  max-width: 44px;
  max-height: 44px;
}

.bx--search {
  margin: 0 !important;
  /* width: 100% !important; */
}

.bx--data-table-container {
  padding: 0;
  /* header 48px, search bar 40px */
  height: calc(100vh - 48px - 40px);
  height: calc(1vh * 100 - 48px - 40px);
  height: calc(var(--vh, 1vh) * 100 - 48px - 40px);
  overflow-y: auto;
}

.bx--data-table-content {
  overflow-x: hidden;
}

img {
  object-fit: cover;
  max-width: 36px;
  max-height: 36px;
  margin: 6px;
}

pre {
  color: red;
}

#Queue {
  display: flex;
  flex-flow: column nowrap;
  flex: 0 0 100%;
}

.currently-playing {
  display: flex;
  flex-flow: row nowrap;
  margin-bottom: 0.6rem;
  height: 3rem;
  background-color: #0f62fe;
}

#MobileQueueWrapper #Queue .currently-playing {
  height: 60px;
}

.currently-playing > div {
  width: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.queue-track {
  display: flex;
  flex-flow: row nowrap;
  margin-bottom: 0.6rem;
  height: 3rem;
}

.queue-track > div:last-child {
  margin-left: auto;
}

.queue-track > div {
  width: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.queue-track-vote-col {
  cursor: pointer;
}

.queue-track-vote-col > div:nth-child(1) {
  margin-bottom: -1rem;
}

.queue-track-vote-col > div:nth-child(1) > svg[class*="active"] {
  fill: #24a148;
}

@media only screen and (max-width: 672px) {
  .bx--grid {
    margin-top: 0px !important;
  }
}

/* html,
body {
  height: 100vh;
  height: calc(var(--vh, 1vh) * 100);
} */
.bx--header {
  border-bottom: 0 !important;
}
.bx--grid {
  max-width: none;
  padding: 0;
}
.bx--row {
  margin: 0 !important;
}
.bx--row > div {
  padding: 0 !important;
}

.bx--header {
  position: static;
}
.bx--header__nav {
  display: block;
}
.bx--search-magnifier {
  z-index: 0;
}

#DesktopQueueWrapper,
#MobileQueueWrapper {
  height: calc(100vh - 48px);
  height: calc(1vh * 100 - 48px);
  height: calc(var(--vh, 1vh) * 100 - 48px);
  background-color: #262626;
  color: #f4f4f4;
}

@media only screen and (max-width: 1056px) {
}

/* Scrollbar */
/* width */
::-webkit-scrollbar {
  width: 6px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #f4f4f4;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #525252;
  border-radius: 1px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #525252;
}

